let ClassManagerComponent = class ClassManagerComponent {
    constructor(http, baseUrl) {
        this.http = http;
        this.baseUrl = baseUrl;
    }
    editClass(editClass) {
        var temp = JSON.parse(JSON.stringify(editClass));
        this.editingClass = temp;
    }
    newClass() {
        this.editingClass = { students: [] };
    }
    saveClass() {
        if (this.editingClass.id) {
            var req = { data: this.editingClass };
            this.http.put(this.baseUrl + 'ClassManager', req).subscribe(result => {
                var temp = this.classes.find(a => a.id === result.data.id);
                temp.name = result.data.name;
                temp.students = result.data.students;
                this.editingClass = null;
            }, error => console.error(error));
        }
        else {
            var req = { data: this.editingClass };
            this.http.post(this.baseUrl + 'ClassManager', req).subscribe(result => {
                this.classes.push(result.data);
                this.editingClass = null;
            }, error => console.error(error));
        }
    }
    cancelEditClass() {
        this.editingClass = null;
    }
    deleteStudent(student) {
        this.editingClass.students = this.editingClass.students.filter(a => a !== student);
    }
    newStudent() {
        if (!this.editingClass.students)
            this.editingClass.students = [];
        this.editingClass.students.push({});
    }
    ngOnInit() {
        this.http.get(this.baseUrl + 'ClassManager').subscribe(result => {
            this.classes = result.data;
        }, error => console.error(error));
    }
};
